<template>
  <div v-if="visible" class="confirm-dialog-overlay">
    <div class="confrim-dialog">
      <h3 class="context">Partner</h3>
      <button @click="confirm" class="btn btn-primary btn-sm"></button>
      <button class="btn btn-secondary" @click="cancel"></button>
    </div>
  </div>
</template>

<script setup>
import { watch, ref, defineProps, defineEmits } from "vue";


const emit = defineEmits(["confirm"]);

const visible = ref(false);

const show = () => {
  visible.value = true;
};

const confirm = () => {
  emit("confirm");
  visible.value = false;
};

const cancel = () => {
  visible.value = false;
};

defineExpose({ show });
</script>
